import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import {
    CCol,
    CContainer,
    CRow,
} from '@coreui/react'
import "src/scss/profile.scss"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCamera, faPen, faUserEdit, } from '@fortawesome/free-solid-svg-icons';
import Avtar from '../../assets/img/avtar.png';
import {Base_URL} from "../../CommonRoutes/Api"
import BlockUI from 'src/CommonRoutes/BlockUI/BlockUI';



class Myprofile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            file: Avtar,
            profile: [],
            profile1:[],
            profile2:[],
            id:localStorage.getItem("user_id"),
            mobile:localStorage.getItem("mobile"),
            name:localStorage.getItem("name"),
            email:localStorage.getItem("email"),
            blocking:false


        }
        this.handleChange = this.handleChange.bind(this)
    }

    componentDidMount() {
        if((!localStorage.getItem('token')) || (!localStorage.getItem('role')) || (!localStorage.getItem('user_id')))
        {
          this.props.history.push("/");
        }
             this.setState({blocking:true})
            fetch(Base_URL + "getuserbyid/" +this.state.id).then((response) => {
                response.json().then((result) => {
                    console.log(result.data);
                    this.setState({profile:result,blocking:false})
                    console.log("profile1",this.state.profile[0]);
                    console.log("profile2",this.state.profile[1][0]);
                    this.setState({profile1:this.state.profile[0],profile2:this.state.profile[1][0]})
                   
                }) 
                
            })    
            
            
    

    }

   

    uploadImage() {

    }
    handleChange(e) {
        this.setState({
            file: URL.createObjectURL(e.target.files[0])
        })
        this.uploadImage();
    }
    render() {
        const { file } = this.state;
        return (
            <div>
                <CContainer style={{ padding: "10px",marginTop: "-1.6rem"}}>
                    <CRow className="justify-content-center" >
                        <CCol md="12" lg="12" xl="12">
                            {/* <CCard className="mx-8">
                <CCardBody className="p-4"> */}
                            <CRow >
                                <CCol md="12" lg="12" xl="12" className="content-center profile_text" style={{backgroundColor:"#110730"}}>
                                    <h4 className="profiletext" style={{color:"orange"}}>My Profile</h4>
                                </CCol>
                            </CRow>
                            <CRow className="content-center" style={{ backgroundColor: "#110730", color: "white" }}>
                                <CCol md="12" lg="12" xl="12" >

                                    <div class="container">
                                        <div class="picture-container">
                                            <div class="picture">
                                                <img src={file} style={{ width: '106px', height: '106px' }} />
                                                <input type="file"
                                                    accept="image/*"
                                                    class=""
                                                    //style={{ display: 'none' }}
                                                    onChange={this.handleChange}
                                                />
                                                 
                                              </div>
                                              <h5 style={{boder:"2px solid blue",borderRadius:"15px"}}>Change</h5>
                                             {/*  <h6 class=""><FontAwesomeIcon icon={faCamera} style={{ fontSize: "23px" }} color="#150465" /></h6> */} 
                                        </div>
                                       
                                     </div>
                                             
                                              </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "#110730", color: "white",marginTop: "-0.1rem" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center  prshadow">
                                    <p className="User_name" style={{ fontWeight: "bold" }}>{this.state.profile1.name}</p>
                                    <p className="User_name" style={{color:"gray",fontWeight:"bold"}}>{this.state.profile1.mobile}</p>
                                    <p className="User_name" style={{color:"gray",fontWeight:"bold", marginBottom: "1rem" }}>{this.state.profile1.email}</p>
                                    <NavLink exact to={`/userupdateprofile/${this.state.id}`}><FontAwesomeIcon icon={faPen} style={{ fontSize: "1.8rem", marginTop: "-3.7rem", position: "relative", float: "right" }} color="orange" /></NavLink>
                                     

                                </CCol>
                            </CRow>

                            <CRow >
                                <CCol md="12" lg="12" xl="12" className="justify-content-left address_bar">
                                    <h4 className="myprofiletext" >Address</h4>
                                </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "#110730", color: "white" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center addressUser">
                                    <p className="addressText" style={{color:"gray",fontWeight:"bold",display: "flex",
    flexWrap: "wrap"}}>{this.state.profile2.address},&nbsp;
                                    <span>{this.state.profile2.city}</span>,&nbsp;
                                   {/*  <span>{this.state.profile2.state}</span> */}
                                    pincode-<span>{this.state.profile2.pincode}</span>
                                    </p>
                                </CCol>
                            </CRow>
                            <CRow >
                                <CCol md="12" lg="12" xl="12" className="justify-content-left address_bar">
                                    <h4 className="myprofiletext" >Rewards</h4>
                                </CCol>
                            </CRow>
                            <CRow style={{ backgroundColor: "#110730", color: "white" }}>
                                <CCol md="12" lg="12" xl="12" className="justify-content-center addressUser">
                                    <p className="rewardText" style={{color:"gray",fontWeight:"bold", marginBottom: "1rem" }}>Rs 0/-   <span style={{ position: "relative", float: "right" }}>(30 Rewards point equal to Rs 1/-)</span></p>
                                    
                                </CCol>
                            </CRow>
                        </CCol>
                    </CRow>
                 </CContainer>
                 <BlockUI blocking={this.state.blocking} />
            </div>
        );
    }
}

export default Myprofile;